import uniIcons from '../uni-icons/uni-icons.vue'
export default {
	name: 'UniCollapseItem',
	components: {
		uniIcons
	},
	props: {
		title: {
			// 列表标题
			type: String,
			default: ''
		},
		name: {
			// 唯一标识符
			type: [Number, String],
			default: 0
		},
		note: {
			type: String,
			default: ''
		},
		disabled: {
			// 是否禁用
			type: Boolean,
			default: false
		},
		showAnimation: {
			// 是否显示动画
			type: Boolean,
			default: false
		},
		open: {
			// 是否展开
			type: Boolean,
			default: false
		},
		isIcon: {
			// 缩略图
			type: Boolean,
			default: false
		},
		icon: {
			type: Object,
			default: () => {
				return {
					type: '',
					color: '',
					size: ''
				}
			}
		}
	},
	data() {
		return {
			isOpen: false
		}
	},
	computed: {
		fiterNote() {
			if (this.note) {
				return this.note.split('|')
			}
		}
	},
	watch: {
		open(val) {
			this.isOpen = val
		}
	},
	inject: ['collapse'],
	created() {
		this.isOpen = this.open
		this.nameSync = this.name ? this.name : this.collapse.childrens.length
		this.collapse.childrens.push(this)
		if (String(this.collapse.accordion) === 'true') {
			if (this.isOpen) {
				let lastEl = this.collapse.childrens[this.collapse.childrens.length - 2]
				if (lastEl) {
					this.collapse.childrens[this.collapse.childrens.length - 2].isOpen = false
				}
			}
		}
	},
	methods: {
		onArrow() {
			if (this.disabled) {
				return
			}
			this.isOpen = !this.isOpen
			this.$emit("onArrow")
		},
		onReturn() {
			if (this.disabled) {
				return
			}
			this.$emit("onReturn")
		}
	}
}
